<template>
<div class="problemDetails">
  <div class="record-bar">
    <van-nav-bar
      title="常见问题"
      left-arrow
      @click-left="linkHome"
    />
  </div>
  <div class="box">
    <div class="title">
      <img src="../../assets/my/problem.png" class="icon"/>
      客户在连续交易未成交的申报单是否延续到下一工作日的日盘交易?
    </div>
    <div class="content">
      <img src="../../assets/my/answer.png" class="icon"/> 是的。开盘集合竞价中未成交的申报单自动参与开盘后的竞价交易。客户的报价在个交易日内一直效,在连续交易期间未成交的申报单直接转入该交易日的日盘,直到该报价全部成交或被撤销。如果客户不想让连续交易期间未成交的申报单进入下一工作日的日盘交易,应在连续交易结束前撤销未成交的申报单。
    </div>
    <div class="relevant">
      <div style="border-top:none;"><div class="edge"></div>相关问题</div>
      <div>交易相关费用说明<van-icon name="arrow" /></div>
      <div>各交易所保证金收取标准<van-icon name="arrow" /></div>
      <div>各交易所交易时间段表<van-icon name="arrow" /></div>
      <div>交易提示说明<van-icon name="arrow" /></div>
      <div>客户在连续交易未成交的申报单是否延续到下工作日的日盘交易?<van-icon name="arrow" /></div>
    </div>
  </div>
</div>
</template>

<script>
export default {
  name: 'problemDetails',
  data () {
    return {

    }
  },
  methods: {
    // 返回上级
    linkHome () {
      this.$router.go(-1)
    }
  }
}
</script>

<style scoped lang="scss">

</style>



